body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

html {
  --link-color-hover: #00acdc;
  --link-color: #000;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-size: 12px;
  display: flex;
}

.nav__link {
  color: var(--link-color);
  text-transform: uppercase;
  margin-left: 20px;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav__link:hover {
  color: var(--link-color-hover);
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.logo__img {
  width: 40px;
  height: 40px;
  display: block;
}

.is-active {
  color: var(--link-color-hover);
  margin: 0;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--link-color-hover);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
